var waypoint1 = new Waypoint({
  element: document.getElementById('our_works'),
  handler: function(direction) {
    $('#our_works ul > li.animated').addClass('wobble')
    $('#our_works h1.animated').addClass('slideInLeft')
    $('#our_works a.animated').addClass('fadeInUp')
  },
  offset: 100
});

