var waypoint5 = new Waypoint({
  element: document.getElementById('team'),
  handler: function(direction) {  	
    $('#team .section-header.animated, #team .section-subheader.animated').addClass('slideInLeft').css("opacity", 1);
    $('#team .team .team-item.animated ').addClass('fadeIn').css("opacity", 1);
    $('#team .team .team-details.animated').addClass('slideInLeft').css("opacity", 1);
  },
  offset: '60%'
});

$('.team-detail.hidden').hide();
$('.team-detail').first().addClass('show');
$('.team-item-inner').hover(function() {
	var index = $(this).closest('.team-item').index();
	var indexedDetail = $(this).closest('.team').find('.team-detail').eq(index);
	indexedDetail.fadeIn().removeClass('hidden').addClass('show');
	$(this).closest('.team').find('.team-detail.show').not(indexedDetail).fadeOut().addClass('hidden').removeClass('show');
});

var waypoint6 = new Waypoint({
  element: document.getElementById('stats'),
  handler: function(direction) {  	
    $('#stats .stats-counter.animated').addClass('fadeIn').css("opacity", 1);

    const options = {
    	suffix: '+'
    };
    let counterArray = [
    	new CountUp('stats-counter-projects', 12458, options),
    	new CountUp('stats-counter-clients', 1796, options),
    	new CountUp('stats-counter-feedbacks', 1000, options),
    	new CountUp('stats-counter-freebies', 1500, options)
    ];
    counterArray.forEach(function(counter) {
    	if (!counter.error) {
    		counter.start();
    	} else {
		console.error(counter.error);
		}
    });
	this.destroy();
  },
  offset: '60%'
});

